.footer {
  background: #222228;
  padding: 60px 0 45px;
  color: #fff;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 70px;
  }
  &__logo {
    width: 279px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__items {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
  }
  &__item {
    &-title {
      font-size: 14px;
      line-height: 160%;
      font-weight: 600;
      letter-spacing: 0.28px;
      margin-bottom: 10px;
      pointer-events: none;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    li a {
      font-size: 14px;
      line-height: 160%;
      font-weight: 300;
      letter-spacing: 0.28px;
      &:hover {
        color: #5856D2;
      }
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 70px;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  &__social {
    width: 42px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #5856D2;
    border-radius: 50%;
    &:hover {
      opacity: 0.7;
    }
    img {
      max-width: 26px;
      max-height: 100%;
    }
  }
  &__text {
    text-align: center;
    font-size: 14px;
    line-height: 160%;
    letter-spacing: 0.28px;
    color: rgba(255, 255, 255, 0.5);
    margin-top: 40px;
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__wrap {
      gap: 48px;
    }
    &__logo {
      width: 220px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 50px 0 70px;
    &__wrap {
      flex-direction: column;
      align-items: center;
      gap: 70px;
    }
    &__logo {
      width: 279px;
    }
    &__items {
      width: 100%;
    }
    &__group {
      margin-top: 60px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    padding: 40px 0;
    &__wrap {
      gap: 40px;
    }
    &__logo {
      width: 240px;
    }
    &__items {
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 20px;
    }
    &__item {
      &-title {
        position: relative;
        display: inline-block;
        padding-right: 24px;
        margin-bottom: 0;
        cursor: pointer;
        pointer-events: auto;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: 0;
          transform: translate(0, -50%);
          width: 15px;
          height: 7px;
          background-image: url("../img/down-arrow2.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto;
          transition: all 0.3s;
        }
      }
    }
    .item-show--active {
      .footer__item-title {
        &:after {
          transform: translate(0, -50%) rotate(-180deg);
        }
      }
    }
    &__list {
      display: none;
      margin-top: 10px;
      li:not(:first-child) {
        margin-top: 10px;
      }
    }
    &__group {
      margin-top: 40px;
    }
  }
}