.header {
  &__registration,
  &__user {
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: #5856D2;
    }
  }
  .nice-select {
    width: auto;
    height: auto;
    background: transparent !important;
    border: none;
    padding: 0 18px 0 0;
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    &:after {
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 11px;
      height: 6px;
      background-image: url("../img/down-arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
    .list {
      z-index: 3;
      width: auto;
      background-color: #fff;
      border: 1px solid #3F3F3F;
      li {
        background-color: transparent !important;
        border: none;
        padding: 4px 8px;
        font-size: 14px;
        line-height: 130%;
        &:not(:last-child) {
          border-bottom: 1px solid #3F3F3F;
        }
      }
      .selected {
        font-weight: 500;
      }
    }
  }
  .open {
    &:after {
      transform: translate(0, -50%) rotate(-180deg);
    }
  }
  &__catalog {
    &-show {
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      background: #5856D2;
      border-radius: 12px 0 0 12px;
      padding: 5px 22px;
      cursor: pointer;
      transition: all 0.3s;
      i {
        width: 20px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 16px;
        line-height: 130%;
        font-weight: 600;
        color: #fff;
      }
    }
  }
  &__search {
    position: relative;
    input {
      width: auto;
      min-width: 200px;
      height: 50px;
      background: #fff;
      border: 1px solid #ECECEC;
      border-radius: 0 12px 12px 0;
      padding: 0 55px 0 25px;
      font-size: 16px;
      line-height: 130%;
      &::placeholder {
        color: #B2B2B2;
      }
    }
    &-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: transparent;
      border-left: 1px solid #ECECEC;
      padding: 5px;
      svg {
        max-width: 80%;
        max-height: 80%;
      }
    }
  }
  &__favourites,
  &__basket {
    display: flex;
    align-items: center;
    gap: 10px;
    &:hover {
      .header__favourites-text,
      .header__basket-text {
        color: #5856D2;
      }
    }
    &-icon {
      position: relative;
      width: 45px;
      height: 45px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EDF1F7;
      border-radius: 50%;
    }
    &-number {
      position: absolute;
      top: -3px;
      right: -3px;
      width: 18.7px;
      height: 18.7px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EA5252;
      border-radius: 50%;
      font-size: 9.6px;
      line-height: 130%;
      font-weight: 700;
      color: #fff;
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      font-weight: 600;
      transition: all 0.3s;
    }
  }
}
.header-top {
  background: #F8F8FC;
  padding: 20px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 30px;
    li a {
      font-size: 14px;
      line-height: 160%;
      font-weight: 300;
      letter-spacing: 0.28px;
      &:hover {
        color: #5856D2;
      }
    }
  }
  &__btn {
    display: flex;
    align-items: center;
    gap: 9px;
    background: transparent;
    padding: 0;
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: #5856D2;
    }
    i {
      width: 29px;
      height: 29px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #5856D2;
      border-radius: 8.7px;
      img {
        max-width: 80%;
        max-height: 80%;
      }
    }
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 30px;
  }
}
.header-bottom {
  background: #fff;
  padding: 20px 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 100px;
  }
  &__logo {
    width: 279px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
    .header-bottom__group {
      display: flex;
    }
  }
  &__group {
    flex-grow: 1;
    display: none;
    align-items: stretch;
  }
  .header__search {
    flex-grow: 1;
    input {
      width: 100%;
    }
  }
  &__group2 {
    display: flex;
    align-items: center;
    gap: 30px;
  }
}
.burger {
  width: 32px;
  display: none;
  img {
    width: 100%;
  }
}
.header-mob {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  display: none;
  background: #fff;
  box-shadow: 0px -6px 40.8px 0px rgba(117, 117, 117, 0.25);
  border-radius: 10px 10px 0 0;
  padding: 15px 20px 10px;
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 16px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-icon {
      position: relative;
      max-width: 24px;
      margin-bottom: auto;
      img {
        max-width: 100%;
      }
    }
    &-number {
      position: absolute;
      top: -4px;
      right: -4px;
      width: 14px;
      height: 14px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EA5252;
      border-radius: 50%;
      font-size: 7px;
      line-height: 130%;
      font-weight: 700;
      color: #fff;
    }
    &-text {
      font-size: 12px;
      line-height: 130%;
      font-weight: 500;
      margin-top: 6px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header-bottom {
    &__wrap {
      gap: 30px;
    }
    &__logo {
      width: 220px;
    }
    &__right {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__favourites,
    &__basket {
      &-icon {
        position: relative;
        width: 35px;
        height: 35px;
        img {
          max-width: 14px;
        }
      }
      &-number {
        top: -2px;
        right: -2px;
        width: 15px;
        height: 15px;
        font-size: 7px;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
  .header-top {
    display: none;
  }
  .header-bottom {
    padding: 16px 0;
    &__wrap {
      gap: 24px;
    }
    &__right {
      flex-grow: unset;
      gap: 24px;
      .header-bottom__group {
        display: none;
      }
    }
    &__group {
      display: flex;
      margin-top: 16px;
    }
    &__group2 {
      gap: 24px;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .header-bottom {
    &__logo {
      width: 200px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header-bottom {
    &__wrap {
      gap: 16px;
    }
    &__logo {
      width: 160px;
    }
    &__right {
      gap: 16px;
      .header-bottom__group {
        display: flex;
        margin-top: 0;
      }
    }
    .header__catalog {
      display: none;
    }
    .header__search {
      input {
        display: none;
      }
      &-btn {
        position: static;
        transform: unset;
        width: 19px;
        height: auto;
        border: none;
        padding: 0;
        svg {
          width: 100%;
          height: auto;
          path {
            stroke: #3F3F3F;
          }
        }
      }
    }
    &__group,
    &__group2 {
      display: none;
    }
  }
  .header-mob {
    display: block;
  }
}